import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import Web3DevelopmentScreen from "../screens/web3-development";

const Web3Development = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Web3 Smart Contract Development Services", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <Web3DevelopmentScreen db={db} />
      </main>
    </Layout>
  );
};
export default Web3Development;

export const Head = () => (
  <Seo
    title="Web3 Smart Contract Development Services"
    description="Elevate your digital presence with cutting-edge Web3 Smart Contract Development services from JustTry Technologies. Explore seamless blockchain solutions, expertly crafted smart contracts, and innovative DApp development. Trust in JustTry Technologies for the future of decentralized excellence."
    keywords="Web3, Smart Contract, Blockchain Development, DApp Development, Solidity, Ethereum, Smart Contract Audit, Token Development, 
   Decentralized Applications, Blockchain Solutions"
  ></Seo>
);

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
